@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@700&family=Roboto:wght@300&display=swap");
:root {
  --color-black: #000;
  --color-white: #fff;
  --color-dark: #151515;
  --quill-grey: #e5e5e3;
  --color-gray: #858585;
  --color-tundora: #454545;
}

/* stylelint-disable */ /* stylelint-disable */ /* stylelint-disable */
.custom-checkbox__field:checked + .custom-checkbox__content::after {
  opacity: 1;
}

.custom-checkbox__field:focus + .custom-checkbox__content::before {
  outline: 2px solid #f00;
  outline-offset: 2px;
}

.custom-checkbox__field:disabled + .custom-checkbox__content {
  opacity: 0.4;
  pointer-events: none;
}

/* stylelint-disable */ /* stylelint-disable */
html {
  box-sizing: border-box;
  scroll-behavior: smooth;
  height: 100%;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  position: relative;
  overflow-anchor: none;
  margin: 0;
  min-width: 320px;
  height: 100%;
  font-family: "Poppins", sans-serif;
  font-size: 16px;
  background-color: var(--background-color);
}

header {
  flex: 0 0 auto;
}

main {
  flex: 1 0 auto;
}

footer {
  flex: 0 0 auto;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.site-container {
  -webkit-text-size-adjust: 100%;
  display: flex;
  flex-direction: column;
  height: 100%;
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;
}
.input-reset::-webkit-search-decoration, .input-reset::-webkit-search-cancel-button, .input-reset::-webkit-search-results-button, .input-reset::-webkit-search-results-decoration {
  display: none;
}

.container {
  position: relative;
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1140px;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.page--ios .dis-scroll {
  position: relative;
}

.to-top {
  position: fixed;
  right: 2%;
  bottom: 5%;
  z-index: 200;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  border-radius: 100%;
  width: 48px;
  height: 48px;
  box-shadow: 0px 5px 30px rgba(0, 0, 0, 0.1);
  background-color: var(--color-white);
}

.primary-mark {
  color: var(--primary-color);
  background: transparent;
}

/* stylelint-disable */
.header {
  margin-top: 80px;
  font-family: Roboto;
  text-align: center;
  text-transform: uppercase;
  color: #FFFFFF;
}
@media (max-width: 1140px) {
  .header {
    margin-top: 50px;
  }
}
@media (max-width: 768px) {
  .header {
    text-align: justify;
    width: 100%;
    margin-top: 2rem;
  }
}
.header__title {
  margin: 0;
  margin-bottom: 9px;
  font-family: "Poppins", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 48px;
  line-height: 110%;
}
@media (max-width: 768px) {
  .header__title {
    font-size: 32px;
  }
}
.header__subtitle {
  margin: 0;
  font-weight: 300;
  font-size: 24px;
  line-height: 110%;
  letter-spacing: 0.05em;
}

.timer {
  margin: 2rem 0;
  font-size: 32px;
}

/* stylelint-disable */
.game__window {
  width: 100%;
  min-height: 200px;
  padding: 31px 0;
  display: flex;
  background-color: var(--window-color);
  border: 3px solid var(--primary-color);
  border-radius: 10px;
}
@media (max-width: 1140px) {
  .game__window {
    flex-direction: column;
  }
}
@media (max-width: 576px) {
  .game__window {
    width: calc(100% + 30px);
