    border-radius: 0;
  }
}
.game__grid {
  position: relative;
  width: 938px;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(2, 1fr);
  justify-content: center;
  gap: 30px;
  height: 50vh;
  padding: 0 45px;
}
@media (max-width: 1140px) {
  .game__grid {
    width: 100%;
  }
