@media (max-width: 768px) {
  .game__grid {
    height: 40vh;
    gap: 20px;
    padding: 0 20px;
  }
}
@media (max-width: 576px) {
  .game__grid {
    height: 22vh;
    gap: 15px;
  }
}
.game__grid:after {
  content: "";
  position: absolute;
  right: 0;
  height: 100%;
  width: 2px;
  background: #25294D;
  border-radius: 10px;
}
@media (max-width: 1140px) {
  .game__grid:after {
    display: none;
  }
}
.game__overlay {
  width: 168px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  gap: 31px;
}
@media (max-width: 1140px) {
  .game__overlay {
    margin-top: 40px;
    width: 100%;
    flex-direction: row;
    align-items: center;
  }
}
.game__btn {
  cursor: pointer;
  transition: transform 0.2s 0s linear;
}
.game__btn svg {
  fill: var(--primary-color);
}
.game__btn:hover {
  transform: translateX(-5px);
}
@media (max-width: 1140px) {
  .game__btn:hover {
