.card {
  cursor: pointer;
  width: 100%;
  height: 100%;
  min-height: 70px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #dedede;
  border: 1px solid var(--primary-color);
  box-shadow: inset 0px 0px 8px 5px rgba(0, 0, 0, 0.25);
  border-radius: 15px;
  transition: 0.15s linear;
}
.card h1 {
  font-size: 5em;
}
@media (max-width: 1140px) {
  .card h1 {
    font-size: 4em;
  }
}
@media (max-width: 768px) {
  .card h1 {
    font-size: 3em;
  }
}
@media (max-width: 576px) {
  .card h1 {
    font-size: 2em;
  }
}
.card:hover {
  transform: translateY(-4px);
  background: #fefefe;
}

.popup-win {
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
  gap: 2rem;
  width: 100vw;
  height: 100vh;
  text-align: center;
  background-color: var(--window-color);
}
.popup-win__link {
  margin: 0;
  font-size: 18px;
  color: #fff;
}
.popup-win__title {
  margin: 0;
  color: #fff;
}
@media (max-width: 100000px) {
  .popup-win .game__overlay {
    width: 100%;
    display: flex;
    flex-direction: row !important;
  }
  .popup-win .game__overlay li:hover {
    transform: rotate(360deg);
  }
}

:root {
  --primary-color: #FFE713;
  --background-color: #0D0F23;
  --window-color: #020413;
}